@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@500;700&family=Jost&family=PT+Sans&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  background-color: #0b0b0e;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
    margin: 0;
    padding: 0;
    font-family: Arial, Helvetica, sans-serif;
}

a{
    text-decoration: none;
}


.trending{
    justify-content: center;
    float: left;
    margin: 10px 10px;
    display: flex;
    flex-wrap: wrap;
    padding: 5px;
}
.trending article{
    color: whitesmoke;
    text-align: center;
    background: transparent;
    margin: 5px;
    margin-bottom: 20px;
    width: 185px;
    font-weight: bold;
}

.trending article:hover{
    color: #eb4034;
    transition: all 0.5s;
}

.trending article img{
    border-radius: 10px;
    transition: all 0.5s;
}
.trending article:hover img{
    -webkit-filter: sepia(50%) brightness(80%) contrast(120%);
}
nav{
    height: 70px;
    border-bottom: 1px solid rgba(255,255,255,.1);
}

.logo-tmdb{
    margin-top: 15px;
    margin-left: 15px;
    width: 100px;
}
*{
    color: whitesmoke;
}

.movie{
    display: flex;
    margin: 15px
}
.movie img{
    border-radius: 10px;
}

.movie-info{
    margin: 10px;
}

.movie-info h1{
    font-family: 'Comfortaa', cursive;
}
.movie-info h3, p{
    font-family: 'Jost', sans-serif;
}

.checked{
    color: orange;
}
